.wrapper {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
  
    .title {
      font-size: 1.8rem;
      font-weight: 700;
      text-align: center;
    }
  
    .error-text {
      color: var(--font-color-warning);
      font-size: 1.2rem;
      text-align: center;
    }
  
    .link {
      width: fit-content;
      margin: 0 auto;
      color: var(--font-color-brand);
      text-decoration: none;
      font-size: 1.4rem;
    }
  
    .link:hover {
      color: var(--font-color-secondary);
    }
  
    form {
      display: flex;
      flex-direction: column;
      gap: 8px;
      background-color: var(--bg-primary-accent);
      padding: 24px;
      border-radius: 8px;
      overflow: hidden;

      hr {
        border: 0;
        height: 1px;
        background-color: var(--bg-separator);
      }
  
      > * {
        border-radius: 3px;
        > * {
          // TextInput
          border-radius: inherit;
        }
      }
    }
  }
  