.wrapper {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-primary-accent);
  flex: 1;

  .content {
    display: flex;
  }

  .main-content {
    flex: 1;
    padding: 0px 24px 24px;
    // min-width: 460px;
    height: calc(100vh - 48px);
    overflow-y: auto;
    background-color: var(--bg-secondary);

    > * {
      margin-bottom: 16px;
    }

    .header {
      position: sticky;
      top: 0px;
      background-color: var(--bg-secondary);
      display: flex;
      flex-direction: column;
      border-bottom: 0.1rem solid var(--bg-separator);

      > :first-child {
        padding-top: 24px;
      }

      > * {
        margin-bottom: 16px;
      }

      .add-task {
        display: flex;
        align-items: center;
        background-color: var(--bg-primary);
        gap: 8px;
        border-radius: 8px;
        overflow: hidden;
        padding-right: 8px;
        > :first-child {
          flex: 1;
        }

        > :last-child {
          border-radius: 8px;
          overflow: hidden;
        }
      }
    }

    .tasks-list {
      > * {
        margin-bottom: 8px;
      }
    }
  }
}

